/* NAVBAR */ /* NAVBAR */ /* NAVBAR */ /* NAVBAR */ /* NAVBAR */
* {
  .navbar {
    width: 100%;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    top: 0;
    z-index: 1;
  }
  .navbar-wrapper {
    display: flex;
  }
  .brand-logo {
    width: 165px;
    height: auto;
  }
  .navbar-menu {
    display: flex;
    transition: all 0.1s ease;
  }
  .theme-toggle-btn {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    display: flex;
    align-items: center;
  }
  @media only screen and (max-width: 800px) {
    .navbar-wrapper {
      align-items: center;
      width: 100%;
      padding: 0 35px;
    }
    .navbar-menu {
      flex-direction: column;
      justify-content: center;
      align-items: end;
      width: 100%;
      padding-right: 30px;
      position: fixed;
      top: 65px;
      right: 0;
      transform: translateX(100%);
      transition: transform 0.1s ease-in-out;
      padding-top: 0px;
      padding-bottom: 5px;
    }
    .navbar-menu.open {
      transform: translateX(0);
    }
    .navbar-menu-text {
      margin: 10px 0;
    }
    .navbar-menu-toggle {
      display: flex;
      cursor: pointer;
      margin-left: auto;
    }
    .navbar-menu-toggle span {
      display: block;
      width: 100%;
      height: 3px;
    }
    .navbar-func {
      display: flex;
      flex-direction: row;
      justify-content: right;
      align-items: end;
      width: 100%;
      gap: 35px;
      padding-right: 30px;
      position: fixed;
      top: calc(142px + 65px);
      right: 0;
      transform: translateX(100%);
      transition: transform 0.1s ease-in-out;
      padding-top: 5px;
      padding-bottom: 20px;
    }
    .navbar-func.open {
      transform: translateX(0);
    }
    .navbar-func-text {
      margin: 10px 0;
    }
  }
  @media only screen and (min-width: 801px) and (max-width: 1199px) {
    .navbar-wrapper {
      align-items: center;
      width: 100%;
      padding: 0 67px;
    }
    .navbar-menu {
      justify-content: right;
      margin-left: auto;
      margin-right: 60px;
      width: 323px;
      gap: 38px;
    }
    .navbar-menu-text {
      display: flex;
      align-items: center;
    }
    .navbar-func {
      display: flex;
      flex-direction: row;
      justify-content: right;
      align-items: end;
      gap: 35px;
      width: 100%;
      padding-right: 75px;
      position: fixed;
      top: 65px;
      right: 0;
      transition: transform 0.1s ease-in-out, visibility 0.05s ease;
      padding-top: 10px;
      padding-bottom: 20px;
      transform: translateY(-30px);
      z-index: -1;
      visibility: hidden;
    }
    .navbar-func.open {
      transform: translateY(0);
      visibility: visible;
      transition: transform 0.1s ease-in-out, visibility 0.05s ease;
    }
    .navbar-func-text {
      margin: 10px 0;
    }
  }
  @media only screen and (min-width: 1200px) {
    .navbar-wrapper {
      width: 1066px;
    }
    .navbar-menu {
      margin-left: auto;
      margin-right: 80px;
      width: 323px;
      gap: 38px;
    }
    .navbar-func {
      display: flex;
      /* margin-left: 35pt; */
      gap: 80px;
    }
    .navbar-menu-text {
      display: flex;
      align-items: center;
      margin-left: auto;
    }
    .navbar-menu-toggle {
      display: none;
    }
    .navbar-func-text {
      display: flex;
      align-items: center;
      margin-left: auto;
    }
  }
}
